@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Global Styles */
:root {
  --primary-color: #22b2da;
}

body {
  background-color: #daeaf6;
}

a {
  color: #22b2da;
  color: var(--primary-color);
  text-decoration: none;
}

a:hover {
  color: #0d8abc;
}

.btn {
  display: inline-block;
  background: var(--light-color);
  color: #333;
  padding: 0.8rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: #22b2da;
  background: var(--primary-color);
  color: #fff;
}

/* Forms */
input {
  margin: 1.2rem 0;
}

.form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

input[type="name"],
input[type="password"] {
  display: block;
  width: 97%;
  padding: 0.4rem;
  font-size: 1.2rem;
}

input[type="submit"],
button {
  font: inherit;
}

.form-group {
  margin: 1.2rem 0;
}

.form-group input {
  margin: 0.2rem 0;
}

.form-container {
  max-width: 500px;
  margin: 2rem auto;
  overflow: hidden;
  padding: 0 2rem;
}

.form-container h1,
h2,
h3,
h5,
h4 {
  text-align: center;
}

